import types from '../types.js'
import axios from 'axios'

const state = {
    seller:{},
    detailshow:false
}

const getters = {
    seller(state){
        return state.seller;
    },
    detailshow(state){
        return state.detailshow
    },
}

const actions = {
    getSeller({commit}){
        axios.get('../../../static/data.json').then(resp =>{
            if(resp.data.errno  === 0){
                commit(types.GET_SELLER,resp.data.seller);
            }
        })
    },
    showdetail({commit}){
        commit(types.SHOW_DETAIL);
    },
    closedetail({commit}){
        commit(types.HIDE_DETAIL);
    }
}

const mutations = {
    [types.GET_SELLER](state,data){
        state.seller = data;
    },
    [types.SHOW_DETAIL](state){
        state.detailshow = true;
    },
    [types.HIDE_DETAIL](state){
        state.detailshow = false;
    }
}

export default{
    state,
    getters,
    actions,
    mutations
}


