<template>
    <div>
        <div class="cartmore"  v-if="showCart">
            <div class="nav">
                <div>购物车</div>
                <div @click="deleteAll">清空</div>
            </div>
            <div class="line"></div>
             <div class="cartfood" ref="aaa">
                 <div class="cartfood1">
                    <div v-for="item in selectFoods" class="cartff">
                        <div>
                            {{item.name}}
                        </div>
                        <div>
                            <span>￥</span><span>{{item.price}}</span>
                            <count :food='item'></count>
                        </div>
                    </div>
                 </div>
             </div>
        </div> 
         <transition enter-active-class = "animated fadeIn" leave-active-class="animated fadeOut">       
            <div class="backg" v-if="showCart" @click="_show"></div>
        </transition> 

        <div class="footer" @click="_show">
            <div class="circle">
                <div class="innercir1" v-if="totalPrice==0" >
                    <i class="iconfont icon-gouwuche"></i>
                </div>
                <div class="innercir2" v-if="totalPrice>0" >
                    <i class="iconfont icon-gouwuche"></i>
                </div>
                <div class="allprice" v-if="totalCount>0">{{totalCount}}</div>
            </div>
            <div class="price">￥{{totalPrice}}</div>
            <div class="middle">
                另需配送费￥{{seller.deliveryPrice}}元
            </div>
            <div class="total" :class="{'enough':totalPrice>=seller.minPrice}">{{payDesc}}</div>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue'
    import {mapGetters} from "vuex"
    import count from '../goods/count.vue'

    export default {
        data() {
            return {
                flag: false
            }
        },
        components:{
            count
        },
        computed:{
            showCart() {
                // console.log(1)
                if(!this.totalCount) {
                    this.flag = false
                    return false
                }
                return this.flag 
            },
            ...mapGetters(['seller','goods']),
            selectFoods(){
                let foods=[];
                this.goods.forEach(good=>{
                    good.foods.forEach(food=>{
                        if(food.count>0){
                            foods.push(food);
                        }
                    })
                })
                return foods
            },
            totalPrice(){
                let total=0;
                this.selectFoods.forEach(food=>{
                    total+=food.price*food.count;
                });
                return total;
            },
            totalCount(){
                let count=0;
                this.selectFoods.forEach(food=>{
                    count+=food.count;
                })
                return count;
            },
            payDesc(){
                if(this.totalCount===0){
                    return `￥${this.seller.minPrice}元起送`
                }else if(this.totalPrice < this.seller.minPrice){
                    return `还差${this.seller.minPrice - this.totalPrice}元起送`
                }else{
                    return "去结算"
                }
            }
        },
        created(){
          
        },
        // watch:{
        //     selectFoods(){
        //         this.$nextTick( () => {
        //             setTimeout(() => {
        //                 if(!this.menuBS){
        //                     this.menuBS = new BScroll(this.$refs.menuWrapper,{click:true});
        //                 }else{
        //                     this.menuBS.refresh();
        //                 }
        //             },1000)
        //         })
        //     }
		// },
        methods:{
             _initMenu() {
                    this.menuBS = new BScroll(this.$refs.aaa,{click:true});
            },
            _show() {
                this.flag=this.totalCount?!this.flag:false;
                if(this.flag) {

                  this.$nextTick( () => {
                    setTimeout(() => {
                        // if(!this.menuBS){
                        //     this.menuBS = new BScroll(this.$refs.aaa,{click:true});
                        // }else{
                        //     this.menuBS.refresh();
                        // }
                        this._initMenu();
                    },100)
                })
                }
                
            },          
            deleteAll(){
                // console.log(1);
               this.selectFoods.forEach((food)=>{
                   food.count = 0;
               })
            }
        }
        

    }
</script>

<style lang="less" scoped>
    .footer{
        height: 1.1rem;
        width: 100%;
        background: #141d27;
        position:fixed;
		bottom:0;
        z-index: 99;
        .circle{
            width: 1.12rem;
			height:1.12rem;
			border-radius: 0.56rem;
			position: absolute;
			left:0.24rem;
			bottom:0.16rem;
			background-color:#1d1427;
            .allprice{
                font-size: 0.18rem;
                font-weight: 700;
                color:white;
                line-height: 0.38rem;
                width: 0.48rem;
                height: 0.36rem;
                background: rgb(240,20,20);
                box-shadow: 0 0.04rem 0.08rem 0 rgba(0,0,0,0.4);
                z-index: 111;
                margin-left:0.78rem; 
                margin-top: -1.1rem;
                border-radius: 0.2rem;
                text-align: center;     
            }
            .innercir1{
                margin: 0.12rem;
                height: 0.88rem;
                width: 0.88rem;
                border-radius: 0.44rem;
                background-color:rgba(255,255,255,0.2);
                text-align: center;
                i{
                    font-size:0.48rem;
                    color:rgba(255,255,255,0.4);
                    line-height:0.78rem;
                }
            }
            .innercir2{
                margin: 0.12rem;
                height: 0.88rem;
                width: 0.88rem;
                border-radius: 0.44rem;
                background-color:rgb(0,160,220);
                text-align: center;
                i{
                    font-size:0.48rem;
                    color:rgb(255,255,255);
                    line-height:0.78rem;
                }
            }
        }
        .price{
            font-size: 0.32rem;
			color:rgba(255,255,255,0.4);
			margin: 0.24rem 0 0.24rem 1.6rem;
			line-height: 0.48rem;
			padding-right: 0.24rem;
			border-right: 0.02rem solid rgba(255,255,255,0.1);
			float:left;
        }
        .middle{
            font-size: 0.24rem;
			color:rgba(255,255,255,0.4);
			margin-left: 0.24rem;
			margin-top: 0.24rem;
			margin-bottom: 0.24rem;
			line-height: 0.52rem;
			float:left;
        }
        .total{
            width:2.10rem;
			height: 1.1rem;
			float:right;
			background-color:rgba(255,255,255,0.2);
			color:rgba(255,255,255,0.4);
			font-size: 0.32rem;
			text-align: center;
			line-height: 1.1rem;
            &.enough{
                color:white;
                background:rgb(0,160,220);

            }
        }
        
    }
    .cartmore{
        max-height: 5.01rem; 
        position:fixed; 
		bottom:1.1rem;
        z-index: 99;
        background:white; 
        overflow:hidden;
        width: 100%; 
        .nav{
            padding:0 0.36rem;
            height: 0.8rem;
            background:#f3f5f7;
            display:flex;
            justify-content:space-between;
            div:nth-of-type(1){
                font-size: 0.28rem;
                font-weight: 200;
                color:rgb(7,17,27);    
                line-height: 0.8rem;
            }
            div:nth-of-type(2){
                font-size: 0.24rem;
                color:rgb(0,160,220);    
                line-height: 0.8rem; 
            }


        }
    }
    .backg{
        position:absolute;
        top:0;
        bottom: 0;
        left:0;
        right: 0;
        background: rgba(7,17,27,0.6);
        filter:blur(0.1rem); 
        z-index:80;
    }
    .line {
        height: 0.03rem;
        background: rgba(7, 17, 27, 0.1);
    }
    .cartfood{
        max-height:4.4rem;
        overflow:hidden;
         .cartfood1{
            max-height:100rem;
            
        } 
        .cartff{
            display: flex;
            justify-content:space-between;
            margin:0 0.36rem;
            padding:0.24rem 0;
            /* height: 0.96rem; */
            border-bottom:0.01rem solid rgba(7, 17, 27, 0.1);
            div:nth-of-type(1){
                font-size: 0.28rem;
                color:rgb(7,17,27);
                line-height: 0.48rem;
            }          
            div:nth-of-type(2){
                display:flex;
                span:nth-of-type(1){
                    font-size:0.2rem;
                    font-weight:normal;
                    color:rgb(240,20,20);
                    line-height:0.48rem;
                }
                span:nth-of-type(2){
                    font-size:0.28rem;
                    font-weight:700;
                    color:rgb(240,20,20);
                    line-height:0.48rem;
                    margin-right: 0.24rem;
                }
            }

        }
    }
</style>


