<template>
	<div class="goods">
		<div class="menu-wrapper" ref = "menuWrapper">
			 <ul >
				   <li v-for="(v,k) in goods" :key='k'  class="menu-list" @click='selectMenu(k,$event)' :class = "{'current' : k === currentIndex}">
                    <span class="text">{{v.name}}</span>
                </li>  
			</ul> 
		</div>
		<div class="goods-foods" ref = "menuWrapper1">
			<div>
				<div v-for="(v,k) in goods" :key='k' class="food-list-hook">
					<span class="title">{{v.name}}</span>
					<div class="detail" v-for="(i,k) in (v.foods)" :key="k" @click = 'showfood(i,$event)'>
						<div class="detailA">

							<div class="detailL">
								<img :src="i.image" alt="">

							</div>
							<div class="detaiR">
								<p>{{i.name}}</p>
								<p>{{i.description}}</p>
								<div class="saleno"><span>月售{{i.sellCount}}份</span><span>好评率{{i.rating}}%</span></div>
								<div class="price">
									<span>￥</span><span>{{i.price}}</span><span v-if="i.oldPrice>0">￥{{i.oldPrice}}</span>
									<div>
										<count :food='i'></count>    
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<foodDetail></foodDetail>
	</div>
</template>

<script>
	import {mapGetters} from 'vuex'
	import foodDetail from '../details/fooddetail.vue'
	// import BScroll from 'better-scroll'
	import count from '../goods/count.vue'

	export default {
		data(){
			return {
				listHeight : [],
				scrollY : 0
			}
		},
		components:{
			foodDetail,
			count
		},
		created(){
			this.$store.dispatch('getGoods');
			// this.$nextTick( () => {
				setTimeout(() => {
					this._initMenu();
					this._calcHeight();
				},0)
			// })
		},
        computed:{
            ...mapGetters(['goods']),
			 currentIndex(){
		    	for( let i = 0 ; i < this.listHeight.length ; i++ ){
		    		let height1 = this.listHeight[i];
		    		let height2 = this.listHeight[i+1];
		    		if( this.scrollY >= height1 && this.scrollY < height2 ){
		    			return i;
		    		}
		    	}
		    	return 0;
		    },
        },
		methods : {
			showfood(f,e){
				if( !e._constructed ){
					return;
				};
				this.$store.dispatch('showfood',f);
				// console.log(1111)
			},

			selectMenu(key,e){
				if( !e._constructed ){
					return;
				}
            	let foodList = this.$refs.menuWrapper1.getElementsByClassName('food-list-hook');
				let el = foodList[key];
				this.foodBS.scrollToElement(el,300);

			},
            _calcHeight(){
            	let foodList = this.$refs.menuWrapper1.getElementsByClassName('food-list-hook');
            	let height = 0;
            	this.listHeight.push(height);
            	for(let i = 0 ; i < foodList.length ; i++){
            		height += foodList[i].clientHeight;
            		this.listHeight.push(height);
            	}
            	// console.log(this.listHeight);
            },
			_initMenu(){
				this.menuBS  = new BScroll(this.$refs.menuWrapper,{click:true});
				this.foodBS = new BScroll(this.$refs.menuWrapper1,{
					probeType : 3,
					click:true

				});
				this.foodBS.on('scroll',(pos) =>{
					this.scrollY = Math.abs(Math.round(pos.y));
					// console.log(this.scrollY);
				})
			}
		},
    }

</script>

<style lang='less' scoped>
	.goods{
		position: absolute;
		top: 3.48rem;
		bottom: 1.1rem;
		width: 100%;
		display: flex;
		overflow: hidden;
		font-size:0.24rem;
		.menu-wrapper{
			flex:0 0 1.6rem;
			width: 1.6rem;
			overflow: hidden;
			.menu-list{
				height: 1.08rem;
				background: #f3f5f7;
				padding:0 0.24rem;
				display: table;
				&.current{
					background: white;
				}
				.text{
					border-bottom: 1px solid rgba(7,17,27,0.1);
					text-align: center;
					vertical-align: middle;
					display: table-cell;
					width: 1.12rem;
				}
			}
		}
		.goods-foods{
			flex:1;
			width: 100%;
			.title{
				font-size: 0.24rem;
				color:rgb(147,153,159);
				line-height:0.52rem;
				height: 0.52rem;
				border-left:0.05rem solid #d9dde1;
				height:0.52rem;
				width: 100%;
				background-color: #f3f5f7;
				display:block;
				padding-left:0.28rem;
			}
			.detail{
				padding:0.36rem 0.36rem 0 0.36rem;
				position: relative;
				.detailA{
					display:flex;
					border-bottom: 0.02rem solid rgba(7,17,27,0.1);
					padding-bottom:0.36rem;
					img{
						height: 1.36rem;
						width: 1.36rem;
						margin-right:0.2rem;
					}
					.detaiR{
						p:nth-of-type(1){
							font-size: 0.28rem;
							color:rgb(7,17,27);
							line-height:0.28rem;
							margin:0.04rem 0 0.16rem 0;
						}
						p:nth-of-type(2){
							font-size: 0.2rem;
							color:rgb(147,153,159);
							line-height:0.2rem;
							margin-bottom: 0.16rem;
						}
						.saleno{
							margin-bottom: 0.16rem;
							span{
								font-size: 0.2rem;
								color:rgb(147,153,159);
								line-height:0.2rem;
							}
							span:nth-of-type(1)	{
								margin-right: 0.24rem;
							}
						}
						.price{
							 div{
								position:absolute;
								right:0.36rem;
								bottom:0.36rem;
							 }
							span:nth-of-type(1){
								line-height: 0.48rem;
								color:red;
								font-weight:700;
								font-size:0.2rem;
							}
							span:nth-of-type(2){
								line-height: 0.48rem;
								color:red;
								font-weight:700;
								font-size:0.28rem;
								margin-right: 0.16rem;
							}
							span:nth-of-type(3){
								line-height: 0.48rem;
								color:rgb(147,153,159);
								font-weight:normal;
								font-size:0.20rem;
								text-decoration: line-through;
							}

						}
					}	
				}

			}
		}
	}
</style>


