<template>
    <div class="header">
         <div class="top"> 
             <div class="background">
                <img :src="seller.avatar" alt="">
            </div>  
            <img :src="seller.avatar" alt="" class="avatar" @click="show">
            <div class="headerMiddle">
                <img src="../../../static/imgs/brand.png" alt="" class="brand">
                <span class="title">{{seller.name}}</span>
                <p class="rightM">{{seller.description}} / {{seller.deliveryTime}}分钟送达</p>
                <div class="rightB">
                    <img src="../../../static/imgs/decrease.png" alt="">
                    <span class="sale" v-if="seller.supports">{{seller.supports[0].description}}</span> 
                </div>
            </div>
            <div class="rightB_r"  @click="show">
                    5个 >
                    <!-- <i class="el-icon-arrow-right"></i>  -->
            </div>
        </div>
        
        <div class="bottom">
            <img src="../../../static/imgs/bulletin.png" alt="" class="bulletin">
              <span class="bulletinText">{{seller.bulletin}}</span> 
             <span class="arrow">></span> 
             
        </div> 
         <Detail></Detail>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    import Detail from '../details/detail.vue'

    export default {
        computed:{
            ...mapGetters(['seller']),
        },
        components:{
            Detail,
        },
        created(){
            this.$store.dispatch('getSeller');
        },
        methods:{
            show(){
                this.$store.dispatch('showdetail');

            }
        }
    }

</script>

<style lang='less' scoped>
    .header{
        height: 2.68rem;
        background-color:rgba(7,17,27,0.5);
        width: 100%;
        position:relative;
        overflow: hidden;
        .top{
             padding-top: 0.48rem;
            padding-left: 0.48rem; 
            .background{
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                z-index: -9;
                img{
                    -webkit-filter: blur(0.1rem);
                    -moz-filter: blur(0.1rem);
                    -o-filter: blur(0.1rem);
                    -ms-filter: blur(0.1rem);
                    width: 100%;
                    height: 100%;
                    
                }
            }
            .headerMiddle{
                position: absolute;
                top:0.52rem;
                left:2.08rem;
                height: 1.28rem;
            }
            .avatar{
                width: 1.28rem;
                height: 1.28rem;
                border-radius:0.04rem;
                z-index: 999;
            }
            .title{
                font-size: 0.32rem;
                color:white;
                font-weight: bold;
                line-height: 0.36rem;
            }
            .brand{
                margin-right:0.12rem; 
                vertical-align:top; 
                height: 0.36rem;
            }
            .rightM{
                font-size: 0.24rem;
                color:white;
                font-weight: 200;
                line-height: 0.24rem;
                margin-top:0.16rem;
                margin-bottom: 0.2rem;
            }
            .rightB{
                
                    img{
                        width: 0.24rem;
                        height: 0.24rem;
                        vertical-align:middle;
                        margin-right:0.08rem;

                    }
                    .sale{
                        font-size: 0.2rem;
                        color:white;
                        font-weight: 200;
                        line-height:0.24rem;
                    }
            
                }
                .rightB_r{
                height: 0.48rem;
                width: 0.9rem;
                background:rgba(0,0,0,0.2);
                font-size:0.2rem;
                color:white;
                font-weight: 200;
                line-height: 0.48rem;
                border-radius: 0.5rem;
                text-align: center;
                position: absolute;
                right:0.8rem;
                bottom:0.8rem;
            
            }       
        }
        .bottom{
                width: 100%;
                height: 0.56rem;
                background: rgba(7,17,27,0.2);
                position: absolute;
                bottom: 0;
                display: flex;
                justify-content: space-around;
                .bulletinText{
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    margin-left: 0.08rem;
                    margin-right: 0.08rem;                    
                    font-size:0.2rem;
                    color:white;
                    font-weight: 200;
                    line-height:0.56rem;
                }
                
                .bulletin{
                    padding-left:0.24rem;
                    height:0.28rem;
                    margin:auto;
                }
                .arrow{
                    color:white;
                    margin-right: 0.24rem;
                    line-height:0.56rem;
                }
        }
    }
</style>



