<template>
    <transition enter-active-class = "animated fadeInLeft" leave-active-class="animated fadeOutRight">
        <div class="goodsdetail" v-if="foodshow" ref="menuWrapper">
            <div>
                <div class="detail-content">
                    <div class="image-header">
                        <img :src="food.image" alt="">
                    </div>
                    <div class="back" @click = 'hidefood'>
                        <i class="icon iconfont icon-shiliangzhinengduixiang4"></i>
                    </div>
                    <div class="detail">
                        <p>{{food.name}}</p>
                        <p>{{food.description}}</p>
                        <div class="saleno"><span>月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span></div>
                        <div class="detailBottom">
                            <div class="price"><span>￥</span><span>{{food.price}}</span><span v-if="food.oldPrice>0">￥{{food.oldPrice}}</span></div>
                            <el-button class="joincart" @click='joincart' v-if="!food.count">加入购物车</el-button>
                            <count :food='food' v-show="food.count"></count>   
                        </div>
                    </div>
                     <div class="line"></div>
                     <div class="intro">
                        <p>商品介绍</p>
                        <div>

                        <span>{{food.info}}</span>
                        </div>
                     </div>
                    <div class="line"></div>
                     <div class="center">
                        <div class="button">
                            <el-button class="btn1" @click="getAll" :class="{'choosed':'getAll' === cc}">全部 <span>{{food.ratings.length}}</span></el-button>
                            <el-button class="btn2" @click="ok" :class="{'ok':'ok' === cc}">推荐 <span>{{countnum}}</span></el-button>
                            <el-button class="btn3" @click="no" :class="{'no':'no' === cc}">吐槽 <span>{{food.ratings.length-countnum}}</span></el-button>
                        </div>
                        <div class="line1"></div>
                        <div class="check">
                            <i class="iconfont icon-duihao" :class="{'checked':flag}" @click="changeFlag"></i>
                            <span>只看有内容的评价</span>
                        </div>
                    </div> 

                    <div class="line2"></div>
                    <div class="content">
                        <ul>
                            <li v-for="(item,k) in ratLists" :key="k" v-show="all || yes === item.rateType">
                                <div class="rat">
                                    <div class="top">
                                        <span class="rateTime">{{item.rateTime | date}}</span>
                                        <div>{{item.username}}
                                        <img :src="item.avatar" alt="" class='pic'>
                                        </div>
                                    </div>
                                    <div class="bottom">
                                        <span v-if="item.rateType == 0" class="good"><i class="iconfont icon-damuzhion"></i></span>
                                        <span v-else="item.rateType == 1" class="bad"><i class="iconfont icon-down"></i></span>
                                        <span class="text">{{item.text}}</span>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>    
            </div>
        </div>
    </transition>
</template>

<script>
    import Vue from 'vue'
    import {mapGetters} from 'vuex'
    import count from '../goods/count.vue'

    export default{
        data() {
            return {
                all: true,
                yes: -1,
                flag: false,
                cc:'getAll'
            }
        },
        components:{
			count
		},
        computed:{
            ...mapGetters(['foodshow','food']),
            ratLists() {
                if (this.flag) {
                    return this.food.ratings.filter((item) => {
                        return item.text
                    })
                }
                return this.food.ratings;
            },
            countnum(){
                // console.log(1)
                return  this.food.ratings.filter((item) => {
                        return item.rateType === 0
                    }).length;
            }
        },
        filters:{
            date:function(value){
                function toDouble (n){
                    return n<10?'0'+n:n;
                }
                var d = new Date(value);
                return d.getFullYear()+"-"+toDouble(d.getMonth()+1)+"-"+toDouble(d.getDate())+" "+toDouble(d.getHours())+":"+toDouble(d.getMinutes());
            }
        },
        watch:{
            food(){
                this.$nextTick( () => {
                    setTimeout( ()=>{
                        if( !this.menuBS ){
                            this.menuBS= new BScroll(this.$refs.menuWrapper, {
                                click: true
                            });
                        }else{
                            this.menuBS.refresh();
                        }
                    },1000)    
                });
            }
        },
        created(){
            // console.log(0)
            // this.$nextTick(() => {
            //     setTimeout(()=>{
            //         this._initScroll();
            //     },100)    
            // });
        },
        methods:{
			hidefood(){
				this.$store.dispatch('hidefood');
			},
            changeFlag() {
                this.flag = !this.flag
                this.refresh()
            },
            refresh() {
                setTimeout(() => {
                    this.menuBS.refresh()
                }, 20)
            },
            ok() {
                this.yes = 0
                this.all = false
                this.refresh()
                this.cc = 'ok'
            },
            no() {
                this.yes = 1
                this.all = false
                this.refresh()
                this.cc = 'no';
            },
            getAll() {
                this.yes = -1
                this.all = true
                this.refresh()
                this.cc = 'getAll';
            },
            joincart(){
                if(!this.food.count){
                    Vue.set(this.food,"count",1);
                }
            }
		}

    }
</script>

<style lang="less" scoped>
     .goodsdetail{
		background-color: white;
		position:fixed;
		top: 0;
		z-index: 79;
		left: 0;
		right: 0;
		bottom: 0;
        margin-bottom: 1.1rem;
		.detail-content{
			.image-header{
				position: relative;
				width: 100%;
				height: 0;
				padding-bottom: 100%;
				img{
					position: absolute;
					width: 100%;
					height: 100%;
					left: 0;
					top:0;
				}
			}
            .back{
				position: fixed;
				top:10px;
				left: 10px;
				width: 10px;
				height: 10px;
				padding:10px;
				color: white;
			}
            .detail{
                padding: 0.36rem;
                p:nth-of-type(1){
                    font-size: 0.28rem;
                    color:rgb(7,17,27);
                    line-height:0.28rem;
                    margin-bottom:0.16rem;
                    font-weight:700;
                }
                p:nth-of-type(2){
                    font-size: 0.2rem;
                    color:rgb(147,153,159);
                    line-height:0.2rem;
                    margin-bottom: 0.36rem;
                }
                .saleno{
                    margin-bottom: 0.16rem;
                    span{
                        font-size: 0.2rem;
                        color:rgb(147,153,159);
                        line-height:0.2rem;
                    }
                    span:nth-of-type(1)	{
                        margin-right: 0.24rem;
                    }
                }
                .detailBottom{
                    display: flex;
                    justify-content: space-between;
                    .price{
                        span:nth-of-type(1){
                            line-height: 0.48rem;
                            color:red;
                            font-weight:700;
                            font-size:0.2rem;
                        }
                        span:nth-of-type(2){
                            line-height: 0.48rem;
                            color:red;
                            font-weight:700;
                            font-size:0.28rem;
                            margin-right: 0.16rem;
                        }
                        span:nth-of-type(3){
                            line-height: 0.48rem;
                            color:rgb(147,153,159);
                            font-weight:normal;
                            font-size:0.20rem;
                            text-decoration: line-through;
                        }

                    }

                     .joincart{
                        width: 1.6rem;
                        height: 0.48rem;
                        border:0;
                        font-size:0.2rem;
                        color:white;
                        text-align:center;
                        line-height:0.12rem;
                        border-radius:0.24rem;
                        background:rgb(0,160,220);
                    } 
                }
            }
            .line {
                height: 0.32rem;
                width: 100%;
                background: #f3f5f7;
                border-top: 1px solid rgba(7, 17, 27, 0.1);
                border-bottom: 1px solid rgba(7, 17, 27, 0.1);
            }
            .intro{
                padding:0.36rem;
                p{
                    font-size:0.28rem;
                    color:rgb(7,17,27);
                    line-height:0.28rem;
                }
                div{
                    margin:0.12rem 0.16rem 0 0.16rem;
                    span{
                        font-size:0.24rem;
                        font-weight: 200;
                        color:rgb(77,85,93);
                        line-height: 0.48rem;
                    }
                }
            }
             .center {
                padding: 0.36rem 0.36rem 0 0.36rem;
                .button {
                    margin-bottom: 0.36rem;
                    .btn1 {
                        font-size: 0.24rem;
                        border-radius: 0.02rem;
                        line-height: 0.32rem;
                        border: 0;
                        color: white;
                        background: rgba(0, 160, 220,0.7);
                        &.choosed{
                            background:rgb(0, 160, 220);
                        }
                    }
                    .btn2 {
                        font-size: 0.24rem;
                        border-radius: 0.02rem;
                        line-height: 0.32rem;
                        border: 0;
                        color: rgb(77, 85, 93);
                        background: rgba(0, 160, 220, 0.2);
                            &.ok{
                                background: rgba(0, 160, 220,0.5);
                            }
                    }
                    .btn3 {
                        font-size: 0.24rem;
                        border-radius: 0.02rem;
                        line-height: 0.32rem;
                        border: 0;
                        color: rgb(77, 85, 93);
                        background: rgba(77, 85, 93, 0.2);
                        &.no{
                            background-color:rgba(77, 85, 93,0.5);
                            color:white;
                        }
                    }
                }
                .line1 {
                    height: 0.02rem;
                    background: rgba(7, 17, 27, 0.1);
                }
            } 
            .check {
                margin: 0.24rem 0;
                i {
                    font-size: 0.48rem;
                    color: rgb(147, 153, 159);
                    line-height: 0.48rem;
                    margin-right: 0.08rem;
                }
                .checked {
                    color: rgb(0, 160, 220);
                } 
                span {
                    font-size: 0.24rem;
                    color: rgb(147, 153, 159);
                    line-height: 0.48rem;
                }
            }
            .line2 {
                height: 0.02rem;
                background: rgba(7, 17, 27, 0.1);
            }
            .content{
                padding:0 0.36rem 0 0.36rem;
               .rat{
                   border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
                   margin-top:0.32rem;
                   .top{
                        display: flex;
                        justify-content: space-between;
                        span{
                            font-size: 0.2rem;
                            color:rgb(147,153,159);
                            line-height: 0.24rem;
                        }
                        div{
                            font-size: 0.2rem;
                            color:rgb(147,153,159);
                            line-height: 0.24rem;
                            img{
                                width: 0.24rem;
                                height: 0.24rem;
                                margin-left:0.12rem;
                                border-radius:0.24rem;
                            }
                        }
                   }
                   .bottom{
                        margin-top:0.12rem;
                        margin-bottom: 0.32rem;
                        .text{
                            font-size:0.24rem;
                            color:rgb(7,17,27);
                            line-height:0.32rem;
                            margin-left:0.08rem;
                        }
                        .good {
                                color: rgb(0, 160, 220);
                                font-size: 0.24rem;
                                line-height: 0.84rem;
                                }
                        .bad {
                            color: rgb(147, 153, 159);
                            font-size: 0.24rem;
                            line-height: 0.84rem;
                        }
                   }
               } 
            }

		}
	}
</style>


