<template>
    <transition enter-active-class = "animated fadeInLeft" leave-active-class="animated fadeOutRight">

        <div class="detail" v-show="detailshow">
            <p class="title">{{seller.name}}</p>
            <i class="iconfont icon-X iconaa" @click="close"></i> 
            <Star :score = 'seller.score' class="star" ></Star> 
            <div class="discounts">
                <div class="discountsL"></div>
                <div class="discountsText">优惠信息</div>
                <div class="discountsR"></div>
            </div>
            <div class="saleinfo">
                <!-- <ul>
                    <li v-for="item in imgSrc">
                        <img :src=imgSrc[findImg()].src  v-show="findImg() > -1">   
                    </li> 
                </ul> -->
                <ul v-if="seller.supports" class="supports">
                    <li class="support-item" v-for="item in seller.supports">
                        <span class="icon" :class="iconClassMap[item.type]"></span>
                        <span class="txt">{{item.description}}</span>
                    </li>
                </ul>
            </div>
            <div class="discounts">
                <div class="discountsL"></div>
                <div class="discountsText">商家公告</div>
                <div class="discountsR"></div>
            </div>
            <div class="content">{{seller.bulletin}}</div>
        </div>
    </transition>
</template>

<script>
    import {mapGetters} from 'vuex'
    import Star from '../star/star.vue'
    
    export default{
        // data(){
        //     return{
        //         imgSrc: [
        //             {type:0, src:'/imgs/decrease.png'},
        //             {type:1, src:'/imgs/discount.png'},
        //             {type:2, src:'/imgs/guarantee.png'},
        //             {type:3, src:'/imgs/invoice.png'},
        //             {type:4, src:'/imgs/special.png'},
        //         ]
        //     }
        // },
        components:{
            Star
        },
        computed:{
            ...mapGetters(['detailshow','seller'])
        },
        created(){
			this.iconClassMap = ["decrease","discount","special","invoice","guarantee"]
		},
        methods:{
            close(){
                this.$store.dispatch("closedetail");
            },
            // findImg(type) {
            //    return imgSrc.findIndex((img)=>{
            //         return img.type === type
            //     })
            // }
        }
    }
</script>

<style lang="less" scoped>
    .detail{
		 position: fixed;
		top:0;
		left: 0;
		right: 0;
		bottom: 0;
		background: rgba(7,17,27,0.8);
        z-index: 9999;
            i{
                position: absolute;
                bottom: 0.64rem;
                color:rgba(255,255,255,0.5);
                font-size: 0.55rem;
                left:45%;
            } 
            .title{
                margin-top:1.28rem;
                color: white;
                font-size:0.32rem;
                font-weight: 700;
                line-height: 0.32rem;
                text-align: center;
                margin-bottom: 0.32rem;                
            }
            
           .discounts{
               /* margin-top:0.56rem; */
               margin: 0 0.72rem;
               display: flex;
               margin-bottom: 0.48rem;
               margin-top:0.56rem;
           }
           .discountsL,.discountsR{
                background: rgba(255,255,255,0.2);
                width: 2.24rem;
                height: 0.03rem;
                margin:auto;  
           }
           .discountsText{
               margin: 0 0.24rem;
               font-size: 0.26rem;
               font-weight:700;
               color:white;
               line-height: 0.24rem;
           }
           .saleinfo{
                margin-left:0.96rem;


           }
           .content{
               font-size: 0.24rem;
               font-weight: 200;
               color:white;
               line-height: 0.48rem;
               margin:0 0.96rem; 
           }
           .supports{
			/* margin-left: 0.96rem; */
            margin-bottom: 0.56rem;
			.support-item{
				 margin-top: 0.24rem;				 
				.icon{
					display: inline-block;
					width: 0.32rem;
					height: 0.32rem;
					padding-right: 0.12rem;
					background-size:0.32rem 0.32rem;
					vertical-align: middle;
					&.decrease{
						background-image:url("../../../static/imgs/decrease.png");
						background-repeat: no-repeat;
					}
					&.discount{
						background-image:url("../../../static/imgs/discount.png");
						background-repeat: no-repeat;
					}
					&.special{
						background-image:url("../../../static/imgs/special.png");
						background-repeat: no-repeat;
					}
					&.invoice{
						background-image:url("../../../static/imgs/invoice.png");
						background-repeat: no-repeat;
					}
					&.guarantee{
						background-image:url("../../../static/imgs/guarantee.png");
						background-repeat: no-repeat;
					}
				}
				.txt{
					font-size: 0.24rem;
					font-weight: 200;
					color:rgb(255, 255, 255);
					line-height: 0.24rem;
				}
			}
		}
        .star{
            text-align: center;
        }
    }

</style> 



